@font-face {
  font-family: "WorldlyBold";
  src:
    url("../fonts/Wordly/WorldlyBold.eot"),
    url("../fonts/Wordly/WorldlyBold.eot?#iefix") format("embedded-opentype"),
    url("../fonts/Wordly/WorldlyBold.woff2") format("woff2"),
    url("../fonts/Wordly/WorldlyBold.woff") format("woff"),
    url("../fonts/Wordly/WorldlyBold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

