.button {
  width: 223px;
  height: 50px;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  position: relative;
  font-size: 15px;
  font-weight: 400;
  text-transform: uppercase;
  font-family: WorldlyRegular;
  outline: none;
  &:focus,
  &:active {
    outline: none;
  }
  &:hover {
    transition: 0.3s;
    bottom: -2px;
    box-shadow: 0 3px rgba(36, 64, 111, 0.25);
  }
  @media (max-width: 767px) {
    font-size: 14px;
  }
  &__white {
    bottom: 0;
    background-color: #fff;
    transition: 0.3s;
  }
  &__green {
    background-color: #0d8572;
    color: white !important;
    bottom: 0;
    transition: 0.3s;
  }
}
