.selection {
  background-color: $colour-bg-light;
  margin-top: 125px;
  @include media-breakpoint-down(md) {
    margin-top: 80px;
  }

  form {
    h2 {
      padding-top: 68px;
      margin-bottom: 31px;
      @include media-breakpoint-down(md) {
        text-align: center;
        padding-top: 32px;
      }
    }
  }

  .card {
    cursor: pointer;
    @include media-breakpoint-down(xl) {
      margin-bottom: 40px;
    }
  }

  .card-header {
    top: -15px;
    color: white;
    font-family: WorldlyLight;
    font-size: 22px;
    font-weight: 300;
    background-color: $colour-bg-dark;
    width: 85%;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    transition: 0.3s;
    @include media-breakpoint-down(md) {
      font-size: 16px;
    }
    @include media-breakpoint-down(sm) {
      font-size: 14px;
      padding: 5px 10px;
    }
  }

  .other-color {
    transition: 0.3s;
    background-color: #c74298 !important;
  }

  .card-body {
    display: flex;
    bottom: 20px;
    width: 100%;

    label {
      display: flex;
      align-items: center;
      width: 85%;
      margin-left: 7.5%;
      height: 44px;
      border-radius: 50px;
      border: 1px solid #0d8572;
      background-color: white;
      font-family: OpenSansRegular;
      color: #0d8572;
      font-size: 15px;
      cursor: pointer;
      position: relative;
      padding-left: 40px;
      margin-right: 0;
      line-height: 18px;
      user-select: none;
      transition: 0.3s;
    }

    label:before {
      content: "";
      display: inline-block;
      width: 15px;
      height: 15px;
      position: absolute;
      left: 15px;
      bottom: 13px;
      background: url("data:image/svg+xml;base64,") 0 0 no-repeat;
    }

    input[type=radio]:checked + label:before {
      background: url("data:image/svg+xml;base64,") 0 0 no-repeat;
    }

    input[type=radio]:checked + label {
      background-color: #0d8572;
      color: white;
      transition: 0.3s;
    }

    label:hover:before {
      filter: brightness(120%);
    }
  }

  .form-footer {
    @include media-breakpoint-down(md) {
      height: 214px;
    }

    &-top {
      @include media-breakpoint-down(md) {
        flex-direction: column;

        a {
          text-align: left;
          margin-bottom: 30px;
        }
      }
    }
  }
}

.selection__footer {
  margin-top: 102px;
  background-color: #f5f5f5;
  @include media-breakpoint-down(md) {
    margin-top: 35px;
  }

  input {
    border: none;
    border-radius: 0;
    background-color: transparent;
    transition: 0.3s;
    border-bottom: 1px solid $colour-bg-dark;

    &:active,
    &:focus {
      background-color: transparent;
      box-shadow: none;
      outline: none;
    }
  }

  .form-control {
    position: absolute;
    width: 100%;
    height: 40px;
    padding: 0;
    top: -11px;
    font-family: OpenSansRegular;
    transition: 0.3s;
  }

  .form-label {
    position: absolute;
    pointer-events: none;
    top: 0;
    transition: 0.3s;
    font-family: OpenSansRegular;
    @include media-breakpoint-down(sm) {
      font-size: 14px;
    }
  }

  .form-control:focus ~ .form-label,
  .form-control:not(:focus):valid ~ .form-label {
    top: -21px;
    font-size: 10px;
  }

  .form-control:focus {
    border-color: #128875;
  }

  .button__green {
    margin-left: 24px;
  }

  .button-box {
    justify-content: end;
    @include media-breakpoint-down(md) {
      justify-content: space-between;
    }
  }
}

.form-footer {
  height: 113px;

  a {
    text-decoration: underline;
    color: #128875;
    font-size: 15px;
    font-weight: 600;
    font-family: OpenSansSemiBold;
    @include media-breakpoint-down(md) {
      text-align: center;
    }
  }
}
