@import "~air-datepicker/dist/css/datepicker.min.css";
.steps {
  position: fixed;
  top: 125px;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 12;
  transition: 0.3s;
  @include media-breakpoint-down(md) {
    top: 80px;
  }
  ul {
    transition: 0.3s;
    position: relative;
    padding-top: 15px;
    padding-bottom: 15px;
    background-color: $colour-bg-light;
    @include media-breakpoint-down(md) {
      padding-top: 35px;
      padding-bottom: 0;
    }
    &:after {
      transition: 0.3s;
      position: absolute;
      content: "";
      height: 13px;
      width: 80%;
      left: 50%;
      transform: translateX(-50%);
      background: #fff;
      z-index: 9;
      top: 78px;
      @include media-breakpoint-down(md) {
        display: none;
        visibility: hidden;
      }
    }

    .current .title-number {
      background-color: #0d8572;
    }
    .current .title-text {
      @include media-breakpoint-down(md) {
        color: #0d8572;
      }
    }
  }

  li {
    a {
      color: #373636;
      font-family: WorldlyRegular;
      font-size: 24px;
      font-weight: 400;
      @include media-breakpoint-down(md) {
        font-size: 16px;
      }
    }
  }

  .title-text {
    transition: 0.3s;
    margin-bottom: 15px;
  }

  .title-number {
    margin: auto;
    width: 38px;
    height: 38px;
    border-radius: 50%;
    border: 5px solid white;
    background-color: white;
    z-index: 10;
    @include media-breakpoint-down(md) {
      display: none;
      visibility: hidden;
    }
  }
}
.date-box {
  p {
    pointer-events: none;
  }
  .label-select {
    @media (min-width: 1200px) and (max-width: 1500px) {
      font-size: 11px !important;
    }
  }
}
.select-box {
  @include media-breakpoint-down(sm) {
    width: 100% !important;
  }
}
.select-box {
  .arrow-down-area {
    &:after {
      right: 5px;
    }
  }
}
.arrow-down-area {
  pointer-events: none;
  &:after {
    content: "";
    position: absolute;
    z-index: 10;
    top: 2px;
    right: -76px;
    width: 10px;
    height: 10px;
    border: solid #238573;
    border-width: 0 1px 1px 0;
    display: inline-block;
    padding: 3px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }
}
.arrow-down-time {
  pointer-events: none;
  &:after {
    content: "";
    position: absolute;
    z-index: 10;
    right: 0;
    top: 3px;
    width: 10px;
    height: 10px;
    border: solid #238573;
    border-width: 0 1px 1px 0;
    display: inline-block;
    padding: 3px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }
}
.select2-search--dropdown {
  display: none;
}
.select-box {
  .select2-container--open .select2-dropdown--below {
    width: 100% !important;
  }
  .select2-container--default {
    width: 100% !important;
  }
}
.select-box-1 {
  .select2-container--open .select2-dropdown--below {
    width: 50% !important;
  }
  .select2-container--default {
    width: 50% !important;
  }
}
.select2-container--default .select2-results__option--highlighted[aria-selected] {
  background-color: $colour-bg-light;
  color: $colour-bg-dark;
}
.select2-results__option[aria-selected]:hover {
  cursor: pointer;
}
.select2-selection__placeholder {
  font-size: 14px !important;
}
.select2-container--focus + .label-select {
  position: absolute !important;
  top: -20px !important;
  transition: 0.3s;
}
.select2-container--open + .label-select {
  position: absolute !important;
  top: -20px !important;
  transition: 0.3s;
}
.select2-container--below + .label-select {
  position: absolute !important;
  top: -20px !important;
  font-size: 8px !important;
  transition: 0.3s;
}
.select2-container .select2-selection--single {
  height: 25px;
}
.select2-container .select2-selection--single .select2-selection__rendered {
  font-size: 15px;
  color: #4a4a4a;
  font-family: OpenSansRegular;
  line-height: 25px;
}
.select2-selection.select2-selection--single {
  outline: none;
}
.select2-selection__rendered {
  padding-left: 0 !important;
}
.select2 .select2-container .select2-container--default .select2-container--below .select2-container--focus .select2-container--open {
  background-color: #fff;
  border: none !important;
  border-bottom: 1px solid $colour-bg-dark !important;
  border-radius: 0 !important;
}
.select2-container--default .select2-selection--single {
  background-color: #fff;
  border: none !important;
  border-bottom: 1px solid $colour-bg-dark !important;
  border-radius: 0;
}
.select2-selection__arrow {
  display: none;
}

.selection__form {
  background-color: white;
  margin-top: 270px;
  @include media-breakpoint-down(md) {
    margin-top: 184px;
  }
  .input-box {
    margin-top: 39px;
    @include media-breakpoint-down(md) {
      margin-top: 25px;
    }
    span {
      color: #d0021b;
      font-family: OpenSansSemiBold;
      font-size: 11px;
      font-weight: 600;
    }
    .label-select {
      pointer-events: none;
      color: #4a4a4a;
      font-size: 15px;
      line-height: 25px;
      z-index: 1;
      top: -5px;
      @include media-breakpoint-down(md) {
        font-size: 11px;
      }
    }
  }
  .invalid-error {
    visibility: hidden;
  }
  .invalid-error-active {
    visibility: visible;
    color: #d0021b;
  }
  input {
    border: none;
    border-radius: 0;
    padding-bottom: 9px;
    background-color: transparent;
    transition: 0.3s;
    border-bottom: 1px solid $colour-bg-dark;

    &:focus, &:active {
      background-color: transparent;
      border-bottom: 1px solid #238573;
      box-shadow: none;
      outline: none;
    }
  }
  input::-webkit-calendar-picker-indicator {
    cursor: pointer;
    z-index: 10;
    background-image: none;
    width: 5px;
    height: 5px;
    border: solid #238573;
    border-width: 0 1px 1px 0;
    display: inline-block;
    padding: 3px;
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
  }

  input[type=date]:invalid {
    color: transparent;
  }
  input[type=time]:invalid {
    color: transparent;
  }
  input[type=text] {
    padding: 0;
  }
  .form-control {
    width: 100%;
    padding: 0;
    top: -7px;
    font-family: OpenSansRegular;
  }

  .form-control:focus {
    border-color: #128875;
  }

  .form-label {
    color: #4a4a4a;
    font-size: 15px;
    line-height: 25px;
    position: absolute;
    top: -4px;
    left: 0;
    transition: 0.3s;
    font-family: OpenSansRegular;
    @include media-breakpoint-down(md) {
      font-size: 11px;
    }
    .hover-box {
      cursor: pointer;
    }

    .hover-box:hover,
    .hover-box:active {
      .tooltip {
        visibility: visible;
        opacity: 1;
      }
    }

    .tooltip {
      visibility: hidden;
      width: 330px;
      border: 1px solid #e1e1e1;
      background-color: #f5f5f5;
      color: #373636;
      font-family: OpenSansRegular;
      font-size: 15px;
      line-height: 17px;
      padding: 20px 15px;
      position: absolute;
      z-index: 11;
      top: -10px;
      left: 90px;
      margin-left: -60px;
      opacity: 0;
      transition: opacity 0.3s;
      @include media-breakpoint-down(sm) {
        width: 200px;
      }
      &:after {
        content: "";
        position: absolute;
        left: 0;
        top: 15%;
        margin-left: -5px;
        border-style: solid;
        background-color: #f5f5f5;
        border-color: #e1e1e1 transparent transparent #e1e1e1;
        transform: rotate(-45deg);
      }
    }
  }
  .time {
    font-size: 12px;
  }
  .form-control:focus ~ .form-label,
  .form-control.not_empty ~ .form-label,
  select.not_empty ~ .label-select,
  .form-control:not(:focus):valid ~ .form-label, {
    top: -21px;
    font-size: 8px;
  }
}
.datepicker-here:focus ~ .label-select,
.timepicker-here:focus ~ .label-select,
.valid ~ .label-select {
  font-size: 8px !important;
  top: -22px !important;
  transition: 0.3s;
}
