.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  z-index: 13;
  background-color: $colour-bg-dark;
  img {
    width: 120px;
    height: 120px;
    @include media-breakpoint-down(md) {
      width: 100px;
      height: 100px;
    }
  }
  a {
    margin-top: 40px;
    margin-right: 40px;
    @include media-breakpoint-down(md) {
      margin-top: 35px;
      margin-right: 30px;
    }
  }
  &__title {
    height: 125px;
    @include media-breakpoint-down(md) {
      height: 80px;
    }
  }
}
